import { useEffect } from 'react'; // image/vnd.microsoft.icon MIMEICO
// image/x-icon GIF
// icoico

var ImgTypeMap = {
  SVG: 'image/svg+xml',
  ICO: 'image/x-icon',
  GIF: 'image/gif',
  PNG: 'image/png'
};

var useFavicon = function useFavicon(favUrl) {
  useEffect(function () {
    if (!favUrl) return;
    var cutUrl = favUrl.split('.');
    var imgSuffix = cutUrl[cutUrl.length - 1].toLocaleUpperCase();
    var link = document.querySelector("link[rel*='icon']") || document.createElement('link');
    link.type = ImgTypeMap[imgSuffix];
    link.href = favUrl; // 'icon' IE'shortcut icon'

    link.rel = 'shortcut icon';
    document.getElementsByTagName('head')[0].appendChild(link);
  }, [favUrl]);
};

export default useFavicon;